<template lang="html">
  <div class="content">
    <el-row type="flex" justify="left">
      <el-col class="video_area">
        <videoItem v-for="item in videoList" :item="item" :key="set_key(item)"></videoItem>
      </el-col>
    </el-row>

    <el-pagination class="pagetag" background layout="prev, pager, next,sizes" :page-size="limit" :page-sizes='[10,20,30]' :pager-count="5" :page-count="total" :current-page="page" @current-change="page_change" @prev-click="page_change_sub" @next-click="page_change_add"
      @size-change="page_size_change"></el-pagination>
  
  </div>
</template>

<script>
import videoItem from "../components/videoItem";

export default {
  data() {
    return {
      tag: []
    };
  },

  computed: {
    videoList() {
      return this.$store.state.videolist;
    },
    total() {
      return this.$store.state.total;
    },
    limit() {
      return this.$store.state.page_size;
    },
    page() {
      return this.$store.state.page;
    }
  },

  methods: {
    getStatus(urlStr) {
      var urlStrArr = urlStr.split("/");
      return Number(urlStrArr[urlStrArr.length - 1]);
    },
    

    getData() {
      this.$store.dispatch("getVideoList");
      window.scrollTo(0, 0);
      this.$router.push(`/page/${this.page}`);
    },

    page_size_change(val) {
      this.$store.commit("page_size_change", val);
      this.limit = val;
      this.getData();
    },

    set_key(item) {
      return item.name;
    },

    page_change_add() {
      this.$store.commit("page_change_add");
      this.getData();
    },

    page_change_sub() {
      this.$store.commit("page_change_sub");

      this.getData();
    },

    page_change(val) {
      this.$store.commit("page_change", val);

      this.getData();
    }
  },

  mounted() {
    this.getData();
  },

  components: {
    videoItem
  },

  watch: {
    $route(to, from) {
      this.page_change(this.getStatus(this.$route.path));
    }
  },

  created() {
    this.getData();
  }
};
</script>

<style lang="css">
.content {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 5%;
}

.video_area {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.pagetag {
  margin-top: 20px;
}
</style>



