import http from './axios'

export function getList(params) {
  return http.get('/video', {
      params
    })
    .then(res => res.data)
}

export function getRandomList(params) {
  return http.get('/video/random', {
    params
    })
    .then(res => res.data)
}

export function getScore(params) {
  return http.get('/video/score', {
    params
    })
    .then(res => res.data)
}

export function isDelete(params) {
  return http.get('/video/isdeleted', {
    params
  }).then(res => res.data)
}

export function setScore(data) {
  return http.put('/video/setScore', { ...data
  }).then(res => res.data)
}

export function recycleBin(params) {
  return http.get('/video/recycleBin', {
    params
  }).then(res => res.data)
}

export function deleteVideo(id) {
  return http.delete(`/video/${id}`).then(res => res.data)
}

export function reviewVideo(id) {
  return http.put(`/video/${id}`).then(res => res.data)
}

export function getName(params) {
  return http.get('/video/name', {
    params
  }).then(res => res.data)
}

export function renewVideodb() {
  return http.put('/video/renew').then(res.res.data)
}



