<template lang="html">
  <div class="random-page">
  
    <videotag v-for="item in randomVideos" :item="item" :key="set_key(item)"></videotag>
  
  </div>
</template>

<script>
import videotag from "./videoItem";

export default {
  data() {
    return {
      videos: []
    };
  },

  computed:{
    randomVideos(){
      return this.$store.state.randomVideoList
    }
  },

  methods: {
    set_key(item) {
      return item.name;
    },

    get_random_video() {
      this.$store.dispatch("getRandomList")
    }
  },

  mounted() {
    this.get_random_video();
  },

  components: {
    videotag
  }
};
</script>

<style lang="css">
.random-page {
}
</style>



