<template lang="html">
  <div class="video" @click="turn_to(item.id)">
    <el-card shadow="hover">
      <div>
        <img :alt="item.name" :src="img_url()">
      </div>
      <p class="video_name">
        {{item.name}}
      </p>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "video_item",
  props: {
    item: {
      type: Object
    }
  },
  data() {
    return {};
  },
  methods: {
    turn_to() {
      this.$router.push({
        name: "video",
        params: {
          id: this.item.id
        }
      });
    },
    img_url() {
      return "/video/img/" + this.item.id;
    }
  }
};
</script>

<style lang="less" scoped>
.video {
  display: inline-block;
  margin-top: 20px;
  margin-right: 20px;
  width: 25%;
  cursor: pointer;
}

.video_name {
  font-size: 1rem;
  word-break: break-all;
  display: block;
}

img {
  max-width: 100%;
  overflow: hidden;
  background-color: #666;
  max-height: 123px;
}
</style>



