<template lang="html">
  <el-header class="centent">
     <el-input class="input_box" v-model="tag" placeholder="tag搜索" prefix-icon="el-icon-search"></el-input>
      <el-button type="primary" icon="el-icon-delete" class="hidden-sm-and-down">回收站</el-button>
      <el-button type="primary" @click="goto_home" class="hidden-sm-and-down">首页</el-button>
  </el-header>
</template>

<script>
import "element-ui/lib/theme-chalk/display.css";
export default {
  data() {
    return {
      tag: ""
    };
  },
  methods: {
    goto_home() {
      this.$router.push("/");
    }
  }
};
</script>

<style lang="css" scoped>
.centent {
  position: fixed;
  top: 0;
  padding-top: 0.63rem;
  width: 100%;
  height: auto;
  z-index: 2;
  background-color: white;
  text-align: center;
}
.input_box {
  max-width: 500px;
  margin-right: 15px;
}
</style>



