import Vue from 'vue'
import Vuex from 'vuex'
import service from '../api'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    page: 1,
    total: 1,
    page_size: 10,
    videolist: [],
    randomVideoList: [],
    randomListLength: 3,
    resycle_page: 1,
    resycle_page_size: 10,
    resycle_list: []
  },
  mutations: {
    page_change_add(state) {
      state.page++
    },
    page_change_sub(state) {
      state.page--
    },
    page_change(state, num) {
      state.page = num
    },
    page_size_change(state, num) {
      state.page_size = num
    },
    change_list(state, data) {
      state.videolist = data
    },
    change_random_list(state, data) {
      state.randomVideoList = data
    },
    change_total(state, data) {
      state.total = data
    },
    change_resycle_list(state, data) {
      state.resycle_list = data
    },
    change_resycle_page_size(state, data) {
      state.resycle_page_size = data
    }
  },
  actions: {
    async getVideoList({
      commit,
      state
    }) {
      const res = await service.getList({
          page: state.page,
          limit: state.page_size
        })
        .catch(err => console.log(err))
      if (res) {
        let {
          list,
          total
        } = res

        commit('change_list', list)
        commit('change_total', total)
      }
    },

    async getRandomList({
      commit,
      state
    }) {
      const res = await service.getRandomList({
          limit: state.randomListLength
        })
        .catch(err => console.log(err))
      if (res) {
        let list = res.list
        commit('change_random_list', list)
      }
    },

    async getScore(id) {
      const res = await service.getScore({
          id
        })
        .catch(err => console.log(err))
      if (res.status) {
        return res.score
      }
    },

    async setScore(id, score) {
      const res = await service.setScore({id, score})
        .catch(err => console.log(err))
      if (res) {
        return res.score
      }
    },

    async isDelete(id) {
      const res = await service.isDelete({id})
        .catch(err => console.log(err))
      if (res) {
        return res.deleted
      }
    },

    async recycleBin({
      commit,
      state
    }) {
      const res = await service.recycleBin({page:state.resycle_page, limit:state.resycle_page_size})
        .catch(err => console.log(err))
      if (res) {
        let {
          list,
          total
        } = res
        commit('change_resycle_list',
          list
        )
        commit('change_resycle_page_size',
          total
        )
      }
    },

    async deleteVideo(id) {
      const res = service.deleteVideo(id)
        .catch(err => console.log(err))
      if (res) {
        return res.status
      }
    },

    async backout_delete(id) {
      const res = service.reviewVideo(id).catch(err => console.log(err))
      if (res) {
        return res.deleted
      }
    },

    async get_name(id) {
      const res = service.getName({id})
        .catch(err => console.log(err))
      if (res) {
        return res.name
      }
    },

    async renewVideodb() {
      const res = service.renewVideodb().catch(err => console.log(err))
      if (res) {
        return res.status
      }
    }
  }
})



